@import './../styles/index.scss';

.servicePageWrapper {
  width: 100%;
  height: 100%;
  background: linear-gradient(
      to top,
      rgba(0, 0, 0, 0.6),
      rgba(0, 0, 0, 0.9),
      rgba(0, 0, 0, 0.9)
    ),
    url('./../assets/images/services-bg4.png');
  background-position: center;
  background-size: contain;
  animation: position 120s linear infinite;
}

@keyframes position {
  100% {
    background-position: 0 50px;
  }
}

.servicesWrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: inline-block;
  // background-color: $c-header;
  padding-top: 100px;
  display: block;
  padding-left: 280px;
  transition: $t-normal;
  @media screen and (min-width: 1700px) and (max-width: 1900px) {
    padding-left: 280px;
  }

  @media screen and (min-width: 1500px) and (max-width: 1700px) {
    padding-left: 260px;
  }

  @media screen and (min-width: $desktop) and (max-width: 1500px) {
    padding-left: 250px;
  }

  @media screen and (max-width: $desktop) {
    padding-left: 0;
  }
}

.servicesWrapperOff {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: inline-block;
  // background-color: $c-header;
  padding-top: 100px;
  display: block;
  padding-left: 0px;
  transition: $t-normal;

  @media screen and (min-width: 1500px) and (max-width: 1700px) {
    padding-left: 30px;
  }

  @media screen and (min-width: 1024px) and (max-width: 1500px) {
    padding-left: 20px;
  }
}

