@import './../../styles/index.scss';

.advantagesWrapper {
  width: 100%;
  height: 100%;
  padding-top: 100px;
  padding-bottom: 50px;
  overflow: hidden;
  display: block;
  background: linear-gradient(to bottom, black, $c-header);

  @media screen and (max-width: $phone) {
    padding-top: 70px;
    padding-bottom: 0;
  }
}

.title {
  text-align: center;
  font-size: 38px;
  color: $c-white;
  margin-top: 60px;
  margin-bottom: 30px;
  text-transform: uppercase;
  transform: scale(1, 1.2);

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 32px;
  }

  @media screen and (max-width: $phone) {
    font-size: 26px;
  }
}

.subtitle {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 70px;
  color: $c-red;
  text-transform: uppercase;

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 18px;
  }

  @media screen and (max-width: $phone) {
    font-size: 16px;
    margin-bottom: 50px;
  }
}

.advantageContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  padding: 30px;

  @media screen and (max-width: $tablet) {
    flex-direction: column;
    margin-bottom: 50px;
  }
}

.textContainer {
  width: 100%;
  max-width: 50%;

  @media screen and (max-width: $tablet) {
    max-width: 100%;
  }
}

.numberContainer {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 40px;

  @media screen and (max-width: $tablet) {
    justify-content: center;
  }
}

.circle {
  background-color: $c-link-hover;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  flex-basis: 60px;
  flex-grow: 0;
  flex-shrink: 0;

  @media screen and (min-width: 900px) and (max-width: 1220px) {
    flex-basis: 50px;
    height: 50px;
  }

  @media screen and (max-width: 900px) {
    flex-basis: 40px;
    height: 40px;
  }
}

.number {
  font-size: 18px;
  color: $c-white;
  font-weight: 700;
}

.advantageTitle {
  font-size: 24px;
  color: $c-white;

  @media screen and (min-width: 900px) and (max-width: 1220px) {
    font-size: 22px;
  }

  @media screen and (min-width: $tablet) and (max-width: 900px) {
    font-size: 18px;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 22px;
  }

  @media screen and (max-width: $phone) {
    font-size: 18px;
  }
}

.text {
  margin-left: 75px;
  font-size: 18px;
  color: $c-white;
  line-height: 28px;

  @media screen and (min-width: $desktop) and (max-width: 1220px) {
    font-size: 16px;
  }

  @media screen and (min-width: 900px) and (max-width: $desktop) {
    margin-left: 0;
    font-size: 16px;
  }

  @media screen and (min-width: $tablet) and (max-width: 900px) {
    margin-left: 0;
    font-size: 14px;
  }

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    margin-left: 0;
    font-size: 16px;
  }

  @media screen and (max-width: $phone) {
    margin-left: 0;
    font-size: 14px;
  }
}

.advantageContainer:nth-of-type(2) {
  @media screen and (max-width: $tablet) {
    flex-direction: column-reverse;
  }
}

.advantageContainer:nth-of-type(3) {
  margin-bottom: 70px;

  @media screen and (max-width: $phone) {
    margin-bottom: 0;
  }
}

.animationContainer {
  @media screen and (min-width: $desktop) and (max-width: 1220px) {
    width: 100%;
    max-width: 450px;
  }

  @media screen and (min-width: $tablet) and (max-width: $desktop) {
    width: 100%;
    max-width: 400px;
  }

  @media screen and (max-width: $tablet) {
    max-width: 400px;
  }
}

.arrow {
  margin: 0 auto;
  display: block;
  width: 100px;
  height: 100px;
  margin-bottom: 20px;

  @media screen and (max-width: $phone) {
    width: 60px;
    height: 60px;
  }
}
