@import './../../styles/index.scss';

.bg {
  position: relative;
  width: 100%;
  height: 100vh;
}

.imageContainer {
  width: 100%;
  height: 100%;
  animation: blur 6s ease-in-out;

  @keyframes blur {
    0% {
      filter: blur(20px); /* Початковий ефект розмиття */
    }
    33% {
      filter: blur(0); /* Зникнення ефекту розмиття */
    }
    100% {
      filter: blur(10px) grayscale(100%) brightness(0); /* Застосування ефекту градації відтінків серого */
    }
  }
}

.image {
  object-fit: cover;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.content {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-left: auto;
  margin-right: auto;
  animation: appear 3s ease-in-out;
  width: 50%;
}

.logoContainer {
  width: 100%;
  height: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}


