@import './../../styles/index.scss';

.headerWrapper {
  position: fixed;
  z-index: 11;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background-color: rgba(255, 255, 255, 0.1);
  animation: appear 1s ease-out;
  transition: $t-normal;

  &:hover {
    // height: 110px;
    background-color: $c-header;

    .logoContainer {
      transform: scale(1.1);
    }
  }

  @media screen and (max-width: $phone) {
    height: 60px;
  }
}

@keyframes appear {
  0% {
    filter: blur(30px);
  }
  100% {
    filter: none;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 20px;
}

.logoContainer {
  margin-top: 10px;
  width: 100px;
  transition: $t-normal;

  @media screen and (max-width: $phone) {
    width: 80px;
  }
}

.logoContainer img {
  width: 100px;

  @media screen and (max-width: $phone) {
    width: 80px;
  }
}

.navVertical {
  position: fixed;
  display: flex;
  flex-direction: column;
  gap: 50px;
  z-index: 20;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  height: 100%;
  padding: 30px;
}

.navClose {
  display: none;
}

.burgerContainer {
  width: 30px;
  height: 30px;
  @media screen and (min-width: $desktop) {
    display: none;
  }
}

.social {
  @media screen and (max-width: $desktop) {
    display: none;
  }
}

.cross {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
}
