@import './../../styles/index.scss';

.bannerWrapper {
  position: relative;
  width: 100%;
  height: 170vh;
  background: linear-gradient(to bottom, rgb(3, 6, 6) 1%, rgba(1, 1, 1, 0.6)),
    url('./../../assets/images/bg-2.png');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}

@keyframes position {
  50% {
    background-position: 0 30px;
  }
}

.contentContainer {
  display: flex;
  flex-direction: column-reverse;
  margin: 0 auto;
  backdrop-filter: blur(10px);
  padding: 50px;
  width: 100%;

  @media screen and (max-width: $tablet) {
    padding: 20px;
  }
}

.banner {
  padding-top: 250px;
}

// ! Title
.title {
  // line-height: 62px;
  font-weight: 700;

  font-family: $ff-main;
  font-size: 56px;
  letter-spacing: 1px;
  color: $c-white;
  margin-bottom: 40px;
  text-transform: uppercase;
  transition: $t-normal;
  // transform: scale(1, 1.3);
  text-align: center;

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 44px;
  }

  @media screen and (max-width: $phone) {
    font-size: 32px;
  }
}

// ! Subtitle
.subtitle {
  line-height: 20px;
  font-weight: 500;
  font-family: $ff-main;
  font-size: 24px;
  color: $c-link-hover;
  letter-spacing: 1px;
  margin-bottom: 100px;
  text-transform: uppercase;
  transition: $t-normal;
  // transform: scale(1, 1.3);
  text-align: center;

  @media screen and (min-width: $phone) and (max-width: $tablet) {
    font-size: 18px;
    margin-bottom: 50px;
  }

  @media screen and (max-width: $phone) {
    font-size: 16px;
    margin-bottom: 50px;
  }
}

.buttons {
  display: flex;
  width: 100%;
  // flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  gap: 110px;

  @media screen and (max-width: $tablet) {
    flex-direction: column;
    gap: 50px;
  }

  //   @media screen and (max-width: $phone) {
  //   font-size: 32px;
  // }
}

.link {
  padding: 10px 30px;
  display: flex;
  align-items: center;
  gap: 10px;
  opacity: 0.9;
  color: $c-white;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  border: 1px solid $c-white;
  border-radius: 30px;
  // box-shadow: $box-shadow;
  transition: $t-normal;
}

.link:hover {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  color: white;
  transform: scale(1.1);
}

.link:active {
  background-color: $c-link-active;
}

.links {
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.linkFirst {
  display: flex;
  align-items: center;
  gap: 4px;
  // padding: 19px 36px;
  color: $c-white;
  font-size: 20px;
  transition: $t-normal;

  @media screen and (min-width: 385px) and (max-width: 805px) {
    font-size: 18px;
  }

  @media screen and (min-width: 356px) and (max-width: 395px) {
    font-size: 16px;
  }

  @media screen and (max-width: 356px) {
    font-size: 16px;
    flex-direction: column;
    align-items: start;
  }
}

.linkFirst span {
  border-bottom: 2px solid transparent;
}

.imgContainerSecond {
  position: absolute;
    display: block;
  bottom: 0;
  overflow: hidden;
}

.imgContainerSecond img {
  display: block;
  overflow: hidden;
}

.textLink {
  display: flex;
  align-items: center;
}

.linkFirst:hover {
  transform: scale(1.1);
}

.linkContainer {
  cursor: pointer;
}

.linkFirst .linkSecond {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.linkSecond {
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: $phone) {
    font-size: 14px;
  }
}

.iconContainer {
  width: 24px;
  height: 24px;

  @media screen and (max-width: $phone) {
    width: 20px;
  }
}

.icon {
  animation: icon 5s ease-in-out;
}

.arrow {
  width: 40px;
  transform: rotate(-90deg);
}

.underline {
  border-bottom: 2px solid red !important;
}

// .content {
//   padding-left: 30px;
//   padding-right: 30px;
// }

.arrowDown {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 50px;
  transition: $t-normal;

  @media screen and (max-width: $phone) {
    width: 60px;
    height: 60px;
    margin-top: 30px;
  }
}
